.note {
  padding: 25px;
  height: 280px;
  width: 260px;
  display: flex;
  flex-direction: column;
  background-color: rgb(144, 91, 193);
  border-radius: 30px;
}

.note_text {
  flex: 1;
  resize: none;
  background-color: transparent;
  font-size: 1rem;
  border: none;
  outline: none;
  line-height: 50px;
}

.note-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.delete-icon {
  font-size: 1.5rem;
  transition: 0.2s ease-in-out;
}

.delete-icon:hover {
  transform: scale(1.5);
}
