.note_container {
  height: 80%;
}

.note_container h2 {
  font-size: 2.1rem;
}

.note_container_notes {
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  overflow-y: scroll;
}

.heading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(249, 245, 245);
  border: 2px solid rgba(89, 87, 87, 0.6);
  box-shadow: 10px 10px 10px 10px rgb(18, 10, 17);
  height: 35%;
  width: 40%;
  padding: 40px;
  margin-block: 50px;
}

.heading img{
  filter: contrast(5);
  margin: 20px;
  width: 40px;
  height: 40px;
}
