.side-bar {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.icons {
  margin-top: 15px;
  font-size: 3rem;
  width: 60px;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.icons:hover {
  transform: scale(1.2);
}

.sidebar-list {
  width: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-left: -30px;
  height: 0;
  transition: 300ms;
}

.sidebar-list-active {
  height: 800px;
}

.li-list {
  border-radius: 50%;
  height: 26px;
  width: 26px;
  list-style: none;
}

